"use client";

import { useState } from "react";
import {
  MdPerson,
  MdEdit,
  MdSave,
  MdClose,
  MdAttachMoney,
  MdNotifications,
  MdSecurity,
  MdLogout,
  MdTrendingUp,
  MdAccessTime,
} from "react-icons/md";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Switch } from "@/components/ui/switch";
import { Separator } from "@/components/ui/separator";
import { Skeleton } from "@/components/ui/skeleton";
import { Slider } from "@/components/ui/slider";
import { cn } from "@/lib/utils";

interface UserProfile {
  id: string;
  name: string | null;
  avatar: string | null;
  bio: string | null;
  email: string | null;
  createdAt: string;
  sellerProfile: {
    id: string;
    hourlyRate: number;
    bio: string | null;
    tagline: string | null;
  };
}

interface SellerSettingsTabProps {
  experienceId: string;
  user: UserProfile;
  isLoading?: boolean;
}

const RATE_PRESETS = [25, 50, 100, 150, 200, 300];

export function SellerSettingsTab({
  experienceId,
  user,
  isLoading,
}: SellerSettingsTabProps) {
  const [isEditingProfile, setIsEditingProfile] = useState(false);
  const [isEditingRate, setIsEditingRate] = useState(false);
  const [isSaving, setIsSaving] = useState(false);

  const [profileData, setProfileData] = useState({
    name: user.name || "",
    bio: user.bio || "",
  });

  const [sellerData, setSellerData] = useState({
    hourlyRate: user.sellerProfile.hourlyRate / 100,
    tagline: user.sellerProfile.tagline || "",
    sellerBio: user.sellerProfile.bio || "",
  });

  const [notifications, setNotifications] = useState({
    newRequests: true,
    sessionReminders: true,
    payoutNotifications: true,
  });

  if (isLoading) {
    return <SettingsLoading />;
  }

  const handleSaveProfile = async () => {
    setIsSaving(true);
    try {
      const response = await fetch("/api/users/profile", {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(profileData),
      });

      if (response.ok) {
        setIsEditingProfile(false);
        window.location.reload();
      }
    } catch (error) {
      console.error("Failed to save profile:", error);
    } finally {
      setIsSaving(false);
    }
  };

  const handleSaveRate = async () => {
    setIsSaving(true);
    try {
      const response = await fetch("/api/sellers/settings", {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          hourlyRate: Math.round(sellerData.hourlyRate * 100),
          tagline: sellerData.tagline,
          bio: sellerData.sellerBio,
        }),
      });

      if (response.ok) {
        setIsEditingRate(false);
        window.location.reload();
      }
    } catch (error) {
      console.error("Failed to save seller settings:", error);
    } finally {
      setIsSaving(false);
    }
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("en-US", {
      style: "currency",
      currency: "USD",
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(amount);
  };

  return (
    <div className="p-4 sm:p-6 md:p-8 max-w-3xl mx-auto">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-2xl font-semibold text-foreground">Settings</h1>
        <p className="text-muted-foreground mt-1">
          Manage your profile and seller preferences
        </p>
      </div>

      {/* Rate Card - Featured */}
      <Card className="mb-6 border-primary/20 bg-gradient-to-br from-primary/5 to-transparent">
        <CardHeader className="pb-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="h-12 w-12 rounded-xl bg-primary/10 flex items-center justify-center">
                <MdAttachMoney className="h-6 w-6 text-primary" />
              </div>
              <div>
                <CardTitle className="text-xl">Your Rate</CardTitle>
                <CardDescription>Set your hourly price</CardDescription>
              </div>
            </div>
            {!isEditingRate && (
              <Button
                variant="outline"
                size="sm"
                onClick={() => setIsEditingRate(true)}
                className="gap-2"
              >
                <MdEdit className="h-4 w-4" />
                Edit
              </Button>
            )}
          </div>
        </CardHeader>
        <CardContent className="space-y-6">
          {isEditingRate ? (
            <>
              {/* Rate Display */}
              <div className="text-center py-4">
                <div className="text-5xl font-bold text-primary mb-2">
                  ${sellerData.hourlyRate}
                  <span className="text-xl font-normal text-muted-foreground">/hr</span>
                </div>
              </div>

              {/* Slider */}
              <div className="px-2">
                <Slider
                  value={[sellerData.hourlyRate]}
                  onValueChange={([value]) => setSellerData({ ...sellerData, hourlyRate: value })}
                  min={10}
                  max={500}
                  step={5}
                  className="w-full"
                />
                <div className="flex justify-between mt-2 text-xs text-muted-foreground">
                  <span>$10</span>
                  <span>$500</span>
                </div>
              </div>

              {/* Preset Buttons */}
              <div className="flex flex-wrap gap-2 justify-center">
                {RATE_PRESETS.map((preset) => (
                  <Button
                    key={preset}
                    variant={sellerData.hourlyRate === preset ? "default" : "outline"}
                    size="sm"
                    onClick={() => setSellerData({ ...sellerData, hourlyRate: preset })}
                    className="min-w-[70px]"
                  >
                    ${preset}
                  </Button>
                ))}
              </div>

              {/* Custom Input */}
              <div className="flex items-center gap-3 justify-center">
                <span className="text-sm text-muted-foreground">Custom:</span>
                <div className="relative w-32">
                  <span className="absolute left-3 top-1/2 -translate-y-1/2 text-muted-foreground">$</span>
                  <Input
                    type="number"
                    min="1"
                    max="1000"
                    value={sellerData.hourlyRate}
                    onChange={(e) => setSellerData({ ...sellerData, hourlyRate: parseFloat(e.target.value) || 0 })}
                    className="pl-7 text-center"
                  />
                </div>
                <span className="text-sm text-muted-foreground">/hr</span>
              </div>

              {/* Earnings Preview */}
              <div className="bg-muted/50 rounded-xl p-4">
                <div className="flex items-center gap-2 mb-3">
                  <MdTrendingUp className="h-4 w-4 text-muted-foreground" />
                  <span className="text-sm font-medium text-muted-foreground">Potential Earnings</span>
                </div>
                <div className="grid grid-cols-3 gap-4 text-center">
                  <div>
                    <p className="text-lg font-semibold text-foreground">
                      {formatCurrency(sellerData.hourlyRate * 2)}
                    </p>
                    <p className="text-xs text-muted-foreground">2 hrs/day</p>
                  </div>
                  <div>
                    <p className="text-lg font-semibold text-foreground">
                      {formatCurrency(sellerData.hourlyRate * 10)}
                    </p>
                    <p className="text-xs text-muted-foreground">10 hrs/week</p>
                  </div>
                  <div>
                    <p className="text-lg font-semibold text-primary">
                      {formatCurrency(sellerData.hourlyRate * 40)}
                    </p>
                    <p className="text-xs text-muted-foreground">40 hrs/mo</p>
                  </div>
                </div>
              </div>

              {/* Save/Cancel */}
              <div className="flex gap-3 justify-end pt-2">
                <Button
                  variant="outline"
                  onClick={() => {
                    setSellerData({
                      ...sellerData,
                      hourlyRate: user.sellerProfile.hourlyRate / 100,
                    });
                    setIsEditingRate(false);
                  }}
                >
                  Cancel
                </Button>
                <Button onClick={handleSaveRate} disabled={isSaving}>
                  {isSaving ? "Saving..." : "Save Rate"}
                </Button>
              </div>
            </>
          ) : (
            <>
              {/* Current Rate Display */}
              <div className="flex items-center justify-between py-2">
                <div>
                  <div className="text-4xl font-bold text-primary">
                    ${user.sellerProfile.hourlyRate / 100}
                    <span className="text-lg font-normal text-muted-foreground">/hr</span>
                  </div>
                  <p className="text-sm text-muted-foreground mt-1">
                    What buyers pay for your time
                  </p>
                </div>
                <div className="text-right">
                  <div className="flex items-center gap-1 text-muted-foreground">
                    <MdAccessTime className="h-4 w-4" />
                    <span className="text-sm">Per hour</span>
                  </div>
                </div>
              </div>
            </>
          )}
        </CardContent>
      </Card>

      {/* Seller Profile Section */}
      <Card className="mb-6">
        <CardHeader>
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="h-10 w-10 rounded-lg bg-muted flex items-center justify-center">
                <MdPerson className="h-5 w-5 text-muted-foreground" />
              </div>
              <div>
                <CardTitle className="text-lg">Seller Profile</CardTitle>
                <CardDescription>How you appear to buyers</CardDescription>
              </div>
            </div>
            {!isEditingProfile ? (
              <Button
                variant="ghost"
                size="sm"
                onClick={() => setIsEditingProfile(true)}
                className="gap-2"
              >
                <MdEdit className="h-4 w-4" />
                Edit
              </Button>
            ) : (
              <div className="flex gap-2">
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => {
                    setProfileData({ name: user.name || "", bio: user.bio || "" });
                    setSellerData({
                      ...sellerData,
                      tagline: user.sellerProfile.tagline || "",
                      sellerBio: user.sellerProfile.bio || "",
                    });
                    setIsEditingProfile(false);
                  }}
                >
                  Cancel
                </Button>
                <Button
                  size="sm"
                  onClick={async () => {
                    await handleSaveProfile();
                    await handleSaveRate();
                  }}
                  disabled={isSaving}
                >
                  {isSaving ? "Saving..." : "Save"}
                </Button>
              </div>
            )}
          </div>
        </CardHeader>
        <CardContent className="space-y-5">
          {/* Avatar & Name Preview */}
          <div className="flex items-center gap-4 p-4 bg-muted/30 rounded-xl">
            <Avatar className="h-14 w-14">
              <AvatarImage src={user.avatar || undefined} />
              <AvatarFallback className="text-lg">
                {user.name?.[0]?.toUpperCase() || "?"}
              </AvatarFallback>
            </Avatar>
            <div className="flex-1">
              {isEditingProfile ? (
                <Input
                  value={profileData.name}
                  onChange={(e) => setProfileData({ ...profileData, name: e.target.value })}
                  placeholder="Your display name"
                  className="font-medium"
                />
              ) : (
                <p className="font-semibold text-lg">{user.name || "Add your name"}</p>
              )}
              <p className="text-xs text-muted-foreground">
                Profile synced from Whop
              </p>
            </div>
          </div>

          {/* Tagline */}
          <div className="space-y-2">
            <Label className="text-muted-foreground text-sm">Tagline</Label>
            {isEditingProfile ? (
              <Input
                value={sellerData.tagline}
                onChange={(e) => setSellerData({ ...sellerData, tagline: e.target.value })}
                placeholder="e.g., Senior Engineer â€¢ Startup Advisor"
                maxLength={100}
              />
            ) : (
              <p className={cn(
                "text-foreground",
                !user.sellerProfile.tagline && "text-muted-foreground italic"
              )}>
                {user.sellerProfile.tagline || "Add a tagline to stand out"}
              </p>
            )}
          </div>

          {/* Bio */}
          <div className="space-y-2">
            <Label className="text-muted-foreground text-sm">About you</Label>
            {isEditingProfile ? (
              <Textarea
                value={sellerData.sellerBio}
                onChange={(e) => setSellerData({ ...sellerData, sellerBio: e.target.value })}
                placeholder="Tell buyers about your expertise, experience, and what you can help with..."
                rows={4}
                maxLength={500}
              />
            ) : (
              <p className={cn(
                "text-foreground whitespace-pre-wrap",
                !user.sellerProfile.bio && "text-muted-foreground italic"
              )}>
                {user.sellerProfile.bio || "Add a bio to help buyers understand what you offer"}
              </p>
            )}
            {isEditingProfile && (
              <p className="text-xs text-muted-foreground text-right">
                {sellerData.sellerBio.length}/500
              </p>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Notifications Section */}
      <Card className="mb-6">
        <CardHeader>
          <div className="flex items-center gap-3">
            <div className="h-10 w-10 rounded-lg bg-muted flex items-center justify-center">
              <MdNotifications className="h-5 w-5 text-muted-foreground" />
            </div>
            <div>
              <CardTitle className="text-lg">Notifications</CardTitle>
              <CardDescription>Manage your alerts</CardDescription>
            </div>
          </div>
        </CardHeader>
        <CardContent className="space-y-1">
          <div className="flex items-center justify-between py-3">
            <div>
              <p className="font-medium text-foreground text-sm">New Booking Requests</p>
              <p className="text-xs text-muted-foreground">When someone wants to book</p>
            </div>
            <Switch
              checked={notifications.newRequests}
              onCheckedChange={(checked) => setNotifications({ ...notifications, newRequests: checked })}
            />
          </div>
          <Separator />
          <div className="flex items-center justify-between py-3">
            <div>
              <p className="font-medium text-foreground text-sm">Session Reminders</p>
              <p className="text-xs text-muted-foreground">Before scheduled sessions</p>
            </div>
            <Switch
              checked={notifications.sessionReminders}
              onCheckedChange={(checked) => setNotifications({ ...notifications, sessionReminders: checked })}
            />
          </div>
          <Separator />
          <div className="flex items-center justify-between py-3">
            <div>
              <p className="font-medium text-foreground text-sm">Payout Notifications</p>
              <p className="text-xs text-muted-foreground">When you receive earnings</p>
            </div>
            <Switch
              checked={notifications.payoutNotifications}
              onCheckedChange={(checked) => setNotifications({ ...notifications, payoutNotifications: checked })}
            />
          </div>
        </CardContent>
      </Card>

      {/* Account Section */}
      <Card>
        <CardHeader>
          <div className="flex items-center gap-3">
            <div className="h-10 w-10 rounded-lg bg-muted flex items-center justify-center">
              <MdSecurity className="h-5 w-5 text-muted-foreground" />
            </div>
            <div>
              <CardTitle className="text-lg">Account</CardTitle>
              <CardDescription>
                Member since {new Date(user.createdAt).toLocaleDateString("en-US", {
                  month: "short",
                  year: "numeric",
                })}
              </CardDescription>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <p className="text-sm text-muted-foreground mb-4">
            Your account is managed through Whop. Visit Whop settings to update your password or account details.
          </p>
          <Button variant="outline" size="sm" className="gap-2" asChild>
            <a href="https://whop.com/settings" target="_blank" rel="noopener noreferrer">
              <MdLogout className="h-4 w-4" />
              Manage on Whop
            </a>
          </Button>
        </CardContent>
      </Card>
    </div>
  );
}

function SettingsLoading() {
  return (
    <div className="p-4 sm:p-6 md:p-8 max-w-3xl mx-auto">
      <div className="mb-8">
        <Skeleton className="h-8 w-32" />
        <Skeleton className="h-4 w-48 mt-2" />
      </div>

      <Card className="mb-6">
        <CardContent className="p-6">
          <div className="flex items-center gap-4 mb-6">
            <Skeleton className="h-12 w-12 rounded-xl" />
            <div className="space-y-2">
              <Skeleton className="h-5 w-24" />
              <Skeleton className="h-3 w-32" />
            </div>
          </div>
          <Skeleton className="h-16 w-full" />
        </CardContent>
      </Card>

      <Card className="mb-6">
        <CardContent className="p-6 space-y-4">
          <div className="flex items-center gap-4">
            <Skeleton className="h-14 w-14 rounded-full" />
            <Skeleton className="h-10 flex-1" />
          </div>
          <Skeleton className="h-10 w-full" />
          <Skeleton className="h-24 w-full" />
        </CardContent>
      </Card>
    </div>
  );
}
